@import 'base';
// @import '~bootstrap/scss/bootstrap.scss';

// Some best-practice CSS that's useful for most apps
// Just remove them if they're not what you want
html {
  box-sizing: border-box;
  font-size: calc(100vw/7.5) !important;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-size: .2rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}



